<template>

  <div>
    <h1>登录界面</h1>
    用户名:<el-input
      placeholder="请输入用户名"
      v-model="username"
      clearable>

    </el-input>
    <br>
    <br>

    密码:
    <el-input
      placeholder="请输入密码"
      v-model="password"
      show-password>
    </el-input>
    <br>
    <br>

    <el-button type="primary"
      plain id="login"
      @click="login">登录
    </el-button>
    <br>
    <br>
    <br>

    <a @click="toRegister"
      href="/#/register">没有账号?立即注册</a>

  </div>
</template>
<script>
import axios from 'axios'
export default {
  name: 'MyLogin',
  data() {
    return {
      username: '',
      password: '',
    }
  },

  methods: {
    toRegister() {
      this.$router.push('/register')
    },
    login() {
      axios({
        method: 'post',
        url: 'http://121.40.113.226:7778/user/login',
        data: {
          username: this.username,
          password: this.password,
        },
      }).then((res) => {
        console.log(res)
        if (res.data.status == 200) {
          this.$store.state.uid = res.data.data.uid
          this.$store.state.username = res.data.data.username
          this.$store.state.nickname = res.data.data.nickname
          // console.log(this.$store.state.uid)
          // console.log(this.$store.state.username)
          // console.log(this.$store.state.nickname)
          this.$router.push('/index')
        } else {
          alert('用户名或密码错误')
        }
      })
    },
  },
  mounted() {
    // console.log(this.$store.state.uid)
    // if (this.$store.state.uid != null&&this.$store.state.uid!==undefined) {
      
    //   this.$router.push('/index')
    // }
  },
}
</script>

<style>
#login {
  position: absolute;
  left: 9.5em;
  width: 8em;
}
</style>
